* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  /* border: 1px solid grey; */
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  display: table;
}

p {
  margin: 0.4rem 5px;
  text-align: center;
}

.card img {
  border-radius: 6px;
  display: table-row;
  margin: 0 auto;
  max-height: 170px;
}